import Vuex from 'vuex'

//信息永久存储
const setlocalcarlist = (state) =>{
  const {cartList} = state
  const cartListstring = JSON.stringify(cartList)
  localStorage.cartList = cartListstring
}

const getlocalstr = ()=>{
  try{
    //解析指定数据
    return JSON.parse(localStorage.cartList)
  }catch(e){
     return{}
  }
 
}

export default Vuex.createStore({
  state: {
    cartList:getlocalstr()
      //第一层商铺id
      // 1:{
      //   111:{
      //     _id: '1',
      //     name: '番茄250g/份',
      //     imgUrl: 'http://www.dell-lee.com/imgs/vue3/tomato.png',
      //     sales: 10,
      //     price: 33.6,
      //     oldPrice: 39.6,
      //     count: 2
      //   },
      //   222:{
      //      _id: '1',
      //     name: '番茄250g/份',
      //     imgUrl: 'http://www.dell-lee.com/imgs/vue3/tomato.png',
      //     sales: 10,
      //     price: 33.6,
      //     oldPrice: 39.6,
      //     count: 2
      //   }
      // },
     
    
  },
  mutations: {
    // additemtocart(state,payload){
    //   const{shopid,productId,productInfo} = payload;
    //   let shopInfo= state.cartList[shopid];
    //   if(!shopInfo) {
    //     shopInfo ={}
    //   }
    //   let product = shopInfo[productId]
    //   if(!product) {
    //     product=productInfo
    //     product.count = 0
    //   }
    //   product.count += 1
    //   shopInfo[productId] = product
    //   state.cartList[shopid] = shopInfo
    // }
    changeiteminfo(state,payload){
      const {shopid,productid,productInfo} = payload;

      let shopInfo = state.cartList[shopid] || {
        shopname:'',productlist:{}
      }
      let product = shopInfo.productlist[productid]
      if(!product) { 
        productInfo.count = 0
        product=productInfo
    }
      product.count = product.count + payload.num;
      if (payload.num > 0 ){product.check = true}
      if(product.count<0){
        product.count = 0
      }
      shopInfo.productlist[productid] = product
      state.cartList[shopid] = shopInfo
      setlocalcarlist(state)
    },
    changeshopname(state,payload){
      const {shopid,shopname} = payload
      const shopInfo = state.cartList[shopid] || {
        shopname:'',productlist:{}
      }
      shopInfo.shopname = shopname
      state.cartList[shopid] = shopInfo
      setlocalcarlist(state)
    },
    changecaritemchanged(state,payload){
      const {shopid,productid} = payload
      const product = state.cartList[shopid].productlist[productid]
   
      product.check = ! product.check
      setlocalcarlist(state)
    },
    clearcarproduct(state,payload){
      const{shopid} = payload
       state.cartList[shopid].productlist = {}
       setlocalcarlist(state)

    },
    setcartchecked(state,payload){
      const {shopid} = payload
      const products = state.cartList[shopid].productlist
      if(products){
        for(let key in products){
          const product = products[key]
          product.check = true
        }
      }
      setlocalcarlist(state)
    },
    clearcardata(state,shopid){
      delete state.cartList[shopid];
    }
  },
})
