import { createRouter, createWebHashHistory } from 'vue-router'
const routes = [
  {
    path: '/',
    name: 'Home',
    component: () => import(/* webpackChunkName: "Home" */ '../views/Home.vue')
  },
  {
    path: '/cartlist',
    name: 'cartlist',
    component: () => import(/* webpackChunkName: "cartlist" */ '../views/cartlist/cartlist')
  },
  {
    path: '/orderconfire/:id',
    name: 'orderconfire',
    component: () => import(/* webpackChunkName: "orderconfire" */ '../views/orderconfire/orderconfire')
  },
  {
    path: '/orderlist',
    name: 'orderlist',
    component: () => import(/* webpackChunkName: "orderlist" */ '../views/orderlist/orderlist')
  },
  {
    path: '/shop/:id',
    name: 'shop',
    component: () => import(/* webpackChunkName: "shop" */ '../views/shop/shop.vue'),
  },
  {
    path: '/login',
    name: 'login',
    component:() => import(/* webpackChunkName: "login" */ '../views/login/login.vue'),
    beforeEnter: (to, from, next) => {
      const isLogin = localStorage.isLogin;
      isLogin ? next({name:'Home'}):next();      
    }
  },{
    path: '/search',
    name: 'search',
    component: () => import(/* webpackChunkName: "search" */ '../views/search/search')
  },
  {
    path: '/searchlist',
    name: 'searchlist',
    component: () => import(/* webpackChunkName: "searchlist" */ '../views/searchlist/searchlist')
  },
  {
    path: '/address',
    name: 'address',
    component: () => import(/* webpackChunkName: "address" */ '../views/address/address')
  },
  {
    path: '/addressedit',
    name: 'addressedit',
    component: () => import(/* webpackChunkName: "addressedit" */ '../views/addressedit/addressedit')
  },
  {
    path: '/addressselect',
    name: 'addressselect',
    component: () => import(/* webpackChunkName: "addressselect" */ '../views/addressselect/addressselect')
  },
  {
    path: '/register',
    name: 'register',
    component:() => import(/* webpackChunkName: "register" */ '../views/register/register.vue'),
    beforeEnter: (to, from, next) => {
      const isLogin = localStorage.isLogin;
      isLogin ? next({name:'Home'}):next();
  },
  
   
}
  // {
  //   path: '/about',
  //   name: 'About',
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //  
  // }
]

const router = createRouter({
  base: './',
  history: createWebHashHistory(),
  routes
})
router.beforeEach((to,from,next)=>{
  const {isLogin} = localStorage;
  const {name } = to;
  const isLoginregister = (name === "login" || name==="register");
  (isLogin || isLoginregister) ? next() : next({name:'login'})
})

export default router
